.header {
  position: relative;
  z-index: 5;
  width: 100%;
  background: #fff;
  border-radius: 10px 10px 0 0;
  &-fixed {
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(10px);
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    padding: 10px 0;
  }
  &__center {
    .header__close,
    .header__logo,
    .header__group2 {
      display: none;
    }
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 30px;
  }
  &__logo {
    width: 179px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__menu {
    display: flex;
    align-items: center;
    gap: 30px;
    li a {
      position: relative;
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -18px;
        width: 0;
        height: 5px;
        background: #FF0015;
        pointer-events: none;
        transition: all 0.225s;
      }
      &:hover {
        &:after {
          width: 100%;
        }
      }
    }
    &-item--active a {
      &:after {
        width: 100% !important;
      }
    }
  }
  &__phone {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    &:hover {
      color: #8D8D8D;
    }
    i {
      width: 13px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  &__info {
    font-size: 12px;
    line-height: 120%;
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 4px;
  }
  &__social {
    width: 35px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    img {
      max-width: 80%;
      max-height: 80%;
    }
  }
  .telegram {
    background: #05A0EB;
    &:hover {
      background: rgba(5, 160, 235, 0.8);
    }
  }
  .whatsapp {
    background: #12C868;
    &:hover {
      background: rgba(18, 200, 104, 0.8);
    }
  }
  &__btn {
    height: 45px;
    border-radius: 121px;
    padding: 5px 30px;
    span {
      font-size: 14px;
    }
  }
}
.burger {
  position: relative;
  width: 32px;
  height: 24px;
  display: none;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #1E1F21;
    border-radius: 3px;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
}

@media screen and (max-width: 1500px) {
  .header {
    &__inner {
      gap: 20px;
    }
    &__right {
      gap: 20px;
    }
    &__logo {
      width: 130px;
    }
    &__menu {
      gap: 20px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .header {
    &__menu {
      li a {
        font-size: 14px;
        &:after {
          bottom: -14px;
          height: 3px;
        }
      }
    }
    &__phone {
      font-size: 16px;
    }
    &__btn {
      height: 40px;
      padding: 5px 20px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .header {
    &__info {
      display: none;
    }
  }
}

@media screen and (max-width: 1150px) {
  .header {
    &__logo {
      width: 100px;
    }
    &__menu {
      li a {
        font-size: 12px;
      }
    }
    &__phone {
      font-size: 12px;
    }
    &__social {
      width: 30px;
      height: 30px;
    }
    &__btn {
      padding: 5px 15px;
      span {
        font-size: 12px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__inner {
      gap: 16px;
    }
    &__center {
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      text-align: center;
      gap: 25px;
      background: #fff;
      border-radius: 10px;
      padding: 15px 15px 25px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
      .header__close,
      .header__logo,
      .header__group2 {
        display: flex;
      }
      .header__logo {
        width: 120px;
      }
      .header__phone {
        font-size: 16px;
        i {
          display: none;
        }
      }
      .header__social {
        width: 35px;
        height: 35px;
        img {
          max-width: 80% !important;
        }
      }
    }
    &__right {
      gap: 16px;
      .header__btn {
        display: none;
      }
    }
    &__logo {
      width: 130px;
    }
    &__menu {
      flex-direction: column;
      gap: 15px;
      li a {
        font-size: 16px;
        &:after {
          display: none;
        }
      }
    }
    &__close {
      position: absolute;
      z-index: 2;
      top: 15px;
      right: 15px;
      width: 29px;
      img {
        width: 100%;
      }
    }
    &__group2 {
      flex-direction: column;
      align-items: center;
      gap: 10px;
    }
    &__btn {
      height: 45px;
      padding: 5px 30px;
      span {
        font-size: 14px;
      }
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__right {
      gap: 6px;
    }
    &__phone {
      font-size: 13.5px;
      i {
        display: none;
      }
    }
    &__social {
      width: 20px;
      height: 20px;
      img {
        max-width: 10px;
      }
    }
  }
  .burger {
    margin-left: 12px;
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__inner {
      gap: 8px;
    }
    &__logo {
      width: 88px;
    }
  }
  .burger {
    margin-left: 8px;
  }
}