.footer {
  background: #1E1F21;
  color: #fff;
  &__items {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
    padding: 50px 0;
  }
  &__item {
    max-width: 340px;
    .footer__text {
      max-width: 230px;
    }
  }
  &__logo {
    width: 230px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__subtitle {
    font-size: 16px;
    line-height: 120%;
    text-transform: uppercase;
    letter-spacing: 6.24px;
    color: rgba(255, 255, 255, 0.5);
  }
  &__list {
    max-width: 230px;
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 16px;
    li a {
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
      &:hover {
        color: #8D8D8D;
      }
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 16px;
    p {
      font-size: 12px;
      line-height: 120%;
    }
  }
  &__phones {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px 40px;
    margin-top: 16px;
  }
  &__phone {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    &:hover {
      color: #8D8D8D;
    }
    i {
      width: 13px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-top: 25px;
  }
  &__info {
    font-size: 12px;
    line-height: 120%;
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 4px;
  }
  &__social {
    width: 35px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    img {
      max-width: 80%;
      max-height: 80%;
    }
  }
  .telegram {
    background: #05A0EB;
    &:hover {
      background: rgba(5, 160, 235, 0.8);
    }
  }
  .whatsapp {
    background: #12C868;
    &:hover {
      background: rgba(18, 200, 104, 0.8);
    }
  }
  &__btn {
    height: 45px;
    border-radius: 121px;
    padding: 5px 68px;
    margin-top: 25px;
    span {
      font-size: 14px;
    }
  }
  &__bottom {
    display: none;
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    &__logo {
      width: 180px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__items {
      display: grid;
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
      gap: 30px 20px;
    }
    &__item {
      max-width: 100%;
      &--big {
        grid-column: 2/4;
      }
    }
    &__logo {
      width: 130px;
    }
    &__phones {
      flex-direction: column;
      align-items: start;
    }
    &__bottom {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      padding: 20px 0;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        width: calc(100% + 40px);
        height: 1px;
        background: rgba(217, 217, 217, 0.2);
        pointer-events: none;
      }
      .footer__text {
        gap: 5px;
        margin-top: 0;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__items {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      gap: 30px;
    }
    &__item {
      &--center {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
      }
    }
    &__list {
      max-width: 140px;
    }
    &__text {
      max-width: 100% !important;
    }
  }
}