.btn-red {
  background: #FF0015;
  color: #fff;
  &:hover {
    background: rgba(255, 0, 21, 0.8);
  }
  span {
    line-height: 120%;
  }
}
.btn-red__outline {
  background: transparent;
  border: 2px solid #FF0015;
  color: #FF0015;
  &:hover {
    background: #FF0015;
    color: #fff;
  }
  span {
    line-height: 120%;
  }
}
.h2 {
  font-size: 48px;
  line-height: 120%;
  font-weight: 500;
}
.swiper-button-prev,
.swiper-button-next {
  z-index: 1;
}
.swiper-pagination {
  z-index: 1;
}
.swiper-pagination-progressbar {
  height: 8px !important;
  background: #F2F5FA;
  border-radius: 10px;
  .swiper-pagination-progressbar-fill {
    background: #FF0015;
    border-radius: 10px;
  }
}
.form {
  .input-title {
    font-size: 14px;
    line-height: 120%;
  }
  &-group {
    input {
      width: 100%;
      height: 68px;
      background: #fff;
      border-color: transparent;
      border-radius: 193px;
      padding: 0 45px;
      font-size: 20px;
      line-height: 120%;
      color: #1E1F21;
      &::placeholder {
        color: #1E1F21;
      }
    }
    textarea {
      width: 100%;
      background: #fff;
      border-color: transparent;
      border-radius: 10px;
      padding: 20px 45px;
      font-size: 20px;
      line-height: 120%;
      color: #1E1F21;
      resize: none;
      &::placeholder {
        color: #1E1F21;
      }
    }
  }
  &-check {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
      }
    }
    label {
      position: relative;
      display: block;
      padding-left: 34px;
      font-size: 14px;
      line-height: 120%;
      &:after {
        content: '';
        position: absolute;
        top: -3px;
        left: 0;
        width: 22px;
        height: 22px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        background-color: #fff;
        border-radius: 5px;
      }
      a {
        position: relative;
        z-index: 3;
        text-decoration: underline;
        &:hover {
          text-decoration: unset;
        }
      }
    }
  }
  &-btn {
    height: 68px;
    border-radius: 193px;
    span {
      font-size: 20px;
    }
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(30, 31, 33, 0.9);
  padding: 0 15px;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: 100%;
    background: #fff;
    border-radius: 10px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 100px auto 50px;
      max-width: calc(100% - 30px);
    }
  }
  &__close {
    position: absolute;
    top: 18px;
    right: 18px;
    width: 58px;
    background: transparent;
    padding: 0;
    img {
      width: 100%;
    }
  }
}
.modal-form {
  .modal__dialog {
    max-width: 550px;
    padding: 30px;
  }
  &__title {
    text-align: center;
    font-size: 32px;
    line-height: 120%;
    font-weight: 500;
  }
  &__subtitle {
    text-align: center;
    font-size: 20px;
    line-height: 120%;
    margin-top: 12px;
  }
  .form {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 20px;
    &-group {
      input {
        border: 1px solid #D9D9D9;
      }
      textarea {
        height: 178px;
        border: 1px solid #D9D9D9;
      }
    }
    &-check {
      label {
        &:after {
          border: 1px solid #D9D9D9;
        }
      }
    }
  }
}
.modal-car {
  .modal__dialog {
    max-width: 1010px;
    padding: 30px 15px;
  }
  &__wrap {
    display: flex;
    align-items: stretch;
    gap: 35px;
  }
  .gallery-wrap {
    position: relative;
    width: calc(100% - 375px);
    min-height: 364px;
  }
  .gallery {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: stretch;
    gap: 15px;
  }
  &__desc {
    max-width: 340px;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .gallery__big {
    width: calc(100% - 15px);
    .swiper-slide {
      padding-bottom: 72%;
    }
  }
  .gallery__small {
    width: 100px;
    order: -1;
    .swiper-slide {
      padding-bottom: 80%;
      img {
        border: 1px solid transparent;
      }
    }
    .swiper-slide-thumb-active {
      img {
        border-color: #FF0015;
      }
    }
  }
  .swiper-slide {
    position: relative;
    height: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
    }
  }
  &__title {
    font-size: 32px;
    line-height: 120%;
    font-weight: 500;
  }
  .car-card__list {
    width: 100%;
    margin-top: 20px;
    margin-bottom: 0;
  }
  &__price {
    font-size: 32px;
    line-height: 120%;
    font-weight: 700;
    margin-top: 40px;
    margin-bottom: auto;
  }
  &__btn {
    width: 100%;
    height: 60px;
    border-radius: 193px;
    padding: 5px 10px;
    span {
      font-size: 16px;
    }
  }
  &__text {
    margin-top: 20px;
    p {
      font-size: 12px;
      line-height: 120%;
    }
  }
}

@media screen and (max-width: 1200px) {
  .modal-car {
    .modal__dialog {
      max-width: 850px;
    }
  }
}

@media screen and (max-width: 992px) {
  .btn-red__outline {
    border-width: 1px;
  }
  .h2 {
    font-size: 32px;
  }
  .form {
    .input-title {
      font-size: 12px;
    }
    &-group {
      input {
        height: 50px;
        padding: 0 20px;
        font-size: 13px;
      }
      textarea {
        padding: 20px;
        font-size: 13px;
      }
    }
    &-btn {
      height: 50px;
      span {
        font-size: 13px;
      }
    }
    &-check {
      label {
        font-size: 10px;
      }
    }
  }
  .modal {
    &__close {
      width: 45px;
    }
  }
  .modal-form {
    .modal__dialog {
      max-width: 400px;
    }
    &__title {
      font-size: 20px;
    }
    &__subtitle {
      font-size: 16px;
    }
  }
  .modal-car {
    .gallery-wrap {
      width: 60%;
      min-height: 300px;
    }
    &__desc {
      width: 40%;
      max-width: 100%;
    }
    &__title {
      font-size: 20px;
    }
    &__price {
      font-size: 20px;
      margin-top: 20px;
    }
    &__btn {
      height: 45px;
      margin-top: 20px;
      span {
        font-size: 13px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .modal-car {
    &__wrap {
      flex-direction: column;
      gap: 20px;
    }
    .gallery-wrap {
      width: 100%;
      min-height: unset;
    }
    &__desc {
      width: 100%;
    }
    .gallery {
      position: static;
      flex-direction: column;
      gap: 10px;
    }
    .gallery__big {
      width: 100%;
    }
    .gallery__small {
      width: 100%;
      order: 2;
      .swiper-slide {
        padding-bottom: 18%;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .h2 {
    font-size: 20px;
  }
  .modal-form {
    .modal__dialog {
      padding: 30px 16px;
    }
  }
}