.main {
  position: relative;
  padding: 85px 0 30px;
  &__top {
    max-width: 1430px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__title {
    font-size: 60px;
    line-height: 120%;
    font-weight: 500;
  }
  &__subtitle {
    max-width: 1065px;
    font-size: 18px;
    line-height: 120%;
    margin-top: 20px;
  }
  &__btn {
    height: 90px;
    border-radius: 193px;
    padding: 10px 64px;
    margin-top: 20px;
    span {
      font-size: 24px;
    }
  }
  &__slider {
    max-width: 1345px;
    margin: 35px auto 0;
    .swiper {
      overflow: unset;
    }
    .swiper-wrapper {
      align-items: stretch;
    }
    .swiper-slide {
      height: auto;
      display: flex;
      align-items: flex-end;
      img {
        max-width: 100%;
      }
    }
    .swiper-button-prev,
    .swiper-button-next {
      &:after {
        font-size: 70px;
        color: rgba(28, 28, 28, 0.25);
      }
      &:hover {
        &:after {
          color: #FF0015;
        }
      }
    }
    .swiper-button-prev {
      left: 155px;
    }
    .swiper-button-next {
      right: 155px;
    }
  }
  &__bottom {
    margin-top: 15px;
    .container {
      max-width: 1830px;
    }
  }
  &__slider2 {
    position: relative;
    padding-bottom: 35px;
    .swiper-wrapper {
      align-items: stretch;
    }
    @media screen and (min-width: 993px) {
      .swiper-slide-active {
        position: relative;
        background: transparent;
        &:after {
          content: '';
          position: absolute;
          z-index: 1;
          top: 0;
          left: 0;
          transform: rotate(-180deg);
          width: 100%;
          height: 100%;
          background: linear-gradient(90.00deg, rgb(34, 34, 34),rgba(34, 34, 34, 0) 50%);
          opacity: 0.03;
          pointer-events: none;
        }
      }
    }
    @media screen and (min-width: 1401px) {
      .swiper-slide-next ~ .swiper-slide ~ .swiper-slide ~ .swiper-slide ~ .swiper-slide {
        position: relative;
        background: transparent;
        &:after {
          content: '';
          position: absolute;
          z-index: 1;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: linear-gradient(90.00deg, rgb(34, 34, 34),rgba(34, 34, 34, 0) 50%);
          opacity: 0.03;
          pointer-events: none;
        }
      }
    }
    .swiper-pagination {
      &-bullet {
        bottom: 0;
        width: 10px;
        height: 10px;
        background: #D9D9D9;
      }
      &-bullet-active {
        background: #FF0015;
      }
    }
  }
  &-brand {
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(34, 34, 34, 0.03);
    padding: 10px;
    img {
      max-width: 80%;
      max-height: 80%;
    }
  }
}
.about {
  padding: 30px 0 60px;
  &__title {
    margin-bottom: 35px;
  }
  &__text {
    max-width: 1440px;
    p {
      font-size: 18px;
      line-height: 120%;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 90px / 4));
    gap: 30px;
    margin: 48px 0 76px 0;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &-number {
      font-size: 113px;
      line-height: 120%;
      font-weight: 500;
      color: #FF0015;
      margin-bottom: 5px;
    }
    .plus {
      position: relative;
      &:before {
        content: '+';
        display: inline;
        color: #FF0015;
      }
    }
  }
  &__text2 {
    text-align: center;
    margin: 0 auto;
  }
}
.info {
  padding: 60px 0 150px;
  &__slider {
    position: relative;
    margin-top: 50px;
  }
  .swiper {
    overflow: unset;
  }
  @media screen and (min-width: 993px) {
    .swiper-wrapper {
      transform: unset !important;
      display: grid;
      grid-template-columns: repeat(4, calc(25% - 96px / 4));
      gap: 56px 32px;
    }
    .swiper-pagination {
      display: none;
    }
  }
}
.info-item {
  &:nth-child(4) {
    @media screen and (min-width: 993px) {
      .info-item__number {
        &:after {
          width: calc(100% - 97px);
        }
        &:before {
          content: '';
          position: absolute;
          top: 50%;
          right: -10px;
          transform: translate(0, -50%);
          border: 5px solid transparent;
          border-left: 5px solid #8D8D8D;
        }
      }
    }
  }
  &__number {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 97px;
      transform: translate(0, -50%);
      width: calc(100% - 65px);
      height: 1px;
      border: 1px dashed #8D8D8D;
    }
    span {
      position: relative;
      z-index: 2;
      width: 97px;
      height: 97px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      border: 3px solid #D9D9D9;
      border-radius: 50%;
      font-size: 40px;
      line-height: 105%;
      font-weight: 500;
    }
  }
  &__number--active {
    span {
      color: #FF0015;
    }
  }
  &__img {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 97px;
      transform: translate(0, -50%);
      width: calc(100% - 65px);
      height: 1px;
      border: 1px dashed #8D8D8D;
    }
    img:nth-child(1) {
      width: 97px;
      height: 97px;
      border-radius: 50%;
    }
    img:nth-child(2) {
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 30px;
      transform: translate(0, -50%);
      max-width: 120px;
    }
  }
  &__title {
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
    margin-top: 40px;
  }
  &__text {
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
    margin-top: 18px;
  }
  &__img2 {
    position: relative;
    width: 97px;
    img {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      max-width: 120px;
    }
  }
  .ellipse {
    position: relative;
    width: 97px;
    height: 97px;
    display: block;
    background: #FF0015;
    border-radius: 50%;
  }
}
.quiz {
  background: #F2F5FA;
  padding: 40px 0;
  &__title {
    display: none;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
    .quiz__title {
      display: block;
    }
  }
  &__left {
    max-width: 900px;
    width: calc(100% - 520px);
  }
  &__right {
    position: relative;
    width: 490px;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 120%;
    margin-top: 20px;
  }
  .progress-bar__wrap {
    position: relative;
    width: 100%;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border-radius: 10px;
    padding: 4px 12px;
    margin-top: 30px;
  }
  .progress-bar {
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: #FF0015;
    border-radius: 10px;
    pointer-events: none;
  }
  .progress-percentage__text {
    position: relative;
    z-index: 2;
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
  }
  .progress-percentage {
    position: relative;
    &:after {
      content: '%';
      display: inline-block;
    }
  }
  &__content {
    margin-top: 30px;
  }
  &-step {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;
    &--active {
      position: static;
      opacity: 1;
      pointer-events: auto;
    }
  }
  &-step__title {
    text-align: center;
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
    span {
      color: #FF0015;
    }
  }
  &__checkboxes {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    margin-top: 24px;
  }
  &__checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 3;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      .quiz__checkbox-img {
        img {
          border-color: #FF0015;
        }
      }
    }
    label {
      display: block;
    }
    &-img {
      position: relative;
      display: block;
      padding-bottom: 37%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 1px solid transparent;
        border-radius: 10px;
      }
    }
    &-title {
      display: block;
      font-size: 24px;
      line-height: 120%;
      font-weight: 700;
      margin-top: 18px;
    }
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    margin-top: 30px;
  }
  &__info {
    max-width: 225px;
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
  }
  &__btn {
    position: static;
    width: auto;
    height: 78px;
    border-radius: 193px;
    padding: 5px 148px;
    margin-top: 0;
    span {
      font-size: 20px;
    }
  }
  &__img {
    width: 100%;
    img {
      width: 100%;
      border-radius: 10px;
    }
  }
  &__group2 {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 60px 40px;
    color: #fff;
  }
  &__subtitle2 {
    font-size: 36px;
    line-height: 120%;
    font-weight: 700;
  }
  &__phone-wrap {
    margin-top: 15px;
  }
  &__phone-text {
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
  }
  &__phone {
    font-size: 36px;
    line-height: 120%;
    font-weight: 700;
  }
  &__form {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 25px / 2));
    gap: 20px 25px;
    margin-top: 30px;
    .form-group {
      display: flex;
      flex-direction: column;
      .input-title {
        display: block;
        font-size: 16px;
        margin-bottom: 10px;
      }
      input {
        height: 59px;
        border-color: transparent;
        border-radius: 30px;
        padding: 0 30px !important;
        margin-top: auto;
        &::placeholder {
          color: rgba(30, 31, 33, 0.6);
        }
      }
    }
  }
  &__form2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
    margin-top: 30px;
    .quiz__form-btn {
      width: 100%;
      height: 90px;
      border-radius: 193px;
      padding: 10px 20px;
      span {
        font-size: 20px;
      }
    }
    .quiz__form-check {
      max-width: 370px;
    }
  }
  &__form-grid {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 3));
    gap: 30px;
  }
  .remove {
    display: none;
  }

}
